.inputInfo {
    color: white
}
.btn {
    background: #3f51b5;
    border: none;
    color: #cecece;
    padding: 4px 5px;
    border-radius: 5px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
}
.btn:hover {
    opacity: 0.9;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}
.input {
    padding: 4px 10px;
    border-radius: 4px;
    border: none;
}


